import { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { Dispatcher } from '../../types/store';
import { clearUploadedOrderStatusData } from '../../store/OrderStatus/actions';

export const useConvertFile = () => {
  return (file: File): Promise<string | ArrayBuffer> => new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => resolve(reader.result);
    reader.onerror = (error) => reject(error);
  });
};

export const useUploadedEntity = (entityId: string | null, uploadedEntity: object, path: string): void => {
  const navigate = useNavigate();
  const dispatch = useDispatch<Dispatcher>();
  useEffect(() => {
    if (uploadedEntity && !entityId) {
      navigate(`/edit/${path}`);
      dispatch(clearUploadedOrderStatusData());
    }
  }, [uploadedEntity]);
};
